@import "./../../scss/variables.scss";

.section-title {
  margin: 0;
  padding: 0;
  color: $black;
  font-size: 60px;
  font-style: normal;
  font-weight: 700;
  line-height: 72px;
  font-family: $font-family-accent;

  &_search {
    font-family: $font-family;
    font-size: 40px;
    font-weight: 400;
    line-height: 48px;
  }

  &_margin-to-subtitle {
    margin-bottom: 20px;
  }

  &_margin-to-block {
    margin-bottom: 32px;
  }
}
